import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import Home from "../components/Home/Home"
import Product from "../components/Product/Product"
import About1 from "../components/About/About1"
import About2 from "../components/About/About2"
import About3 from "../components/About/About3"
import Gigbeat from  "../components/Gigbeat/Gigbeat"
import Contact from  "../components/Contact/Contact"

import ProductList from "../components/Product/ProductList"

import Content from "../components/Content";

const router = new VueRouter({
  mode: 'hash',
  routes:[
      {path:"/",component:Home},
      {path:"/product",component:Product,
          children:[
              {path:"all",component:ProductList},
              {path:"product1",component:ProductList},
              {path:"product2",component:ProductList},
              {path:"product3",component:ProductList}
          ],
          redirect:"/product/all"
      },
      {path:"/about/about1",component:About1},
      {path:"/about/about2",component:About2},
      {path:"/about/about3",component:About3},
      {path:"/about",redirect:"/about/about2"},
      {path:"/gigbeat",component:Gigbeat},
      {path:"/contact",component:Contact},

      /*点开内容 显示详细 路由*/
      {path:"/gigbeat/:Id",component:Content},
      {path:"/product/all/:Id",component:Content},
      {path:"/product/product1/:Id",component:Content},
      {path:"/product/product2/:Id",component:Content},
      {path:"/product/product3/:Id",component:Content},
      {path:"/about/about1/:Id",component:Content},
      {path:"/about/about3/:Id",component:Content}


  ]
})
export default router
















