<template>
    <div class="star"> <!--最新活动区域-->
        <ul>
            <li v-for="item in activity.list" :key="item.Id">
                <router-link :to="item.Url">
                    <div class="Img_box loading">
                        <img :src="item.Img" v-if="item.Img">
                    </div>
                    <div class="Text_box">
                        <div class="titles loading" v-text="item.Title"></div>
                        <div class="descriptions loading" v-text="item.Description!==''?item.Description.substr(0,31)+'...':''"></div>
                    </div>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "About3",
        data(){
            return{
                activity:{/*最新活动的数据*/
                    list:[
                        {Id:1,Title:"",Url:"",Img:"",Description:""},
                        {Id:2,Title:"",Url:"",Img:"",Description:""},
                        {Id:3,Title:"",Url:"",Img:"",Description:""},
                        {Id:4,Title:"",Url:"",Img:"",Description:""},
                    ]
                },
            }
        },
        created(){
            let ajax=new XMLHttpRequest();
            //获取 最新活动 的数据 http://192.168.8.8:82/api/getConentList?Url=/about/about3&page=1
            ajax.open("get",`/api/getConentList?Url=${this.$route.path}&page=1`);
            ajax.send();
            ajax.onload=()=>{
                this.activity.list=JSON.parse(ajax.responseText).content;
            }
        }
    }
</script>

<style scoped>
    .star ul li{
        padding:0.26rem 0;
        border-bottom: 1px solid #ddd;
    }
    .star ul{ padding:0 0.2rem}
    .star ul li a{
        display: block;
    }
    .star ul li a .Img_box{
        width:100%;
        min-height: 1rem;
    }
    .star ul li a .Img_box img{
        width:inherit;
    }
    .star ul li a .Text_box{
        height: 100%;
        margin-left: 0.13rem;
        flex-grow: 1;
    }
    .star ul li a .Text_box .titles{
        height: 0.28rem;

    }
    .star ul li a .Text_box .types{
        height: 0.28rem;
        margin:1px 0;
    }
    .star ul li a .Text_box .descriptions{
         font-size: 0.14rem;
        color: #555;
    }
</style>