<template>
    <div class="Content">
        <!--msg是加载显示的文字-->
        <!--:status用来控制这个加载是否显示-->
        <Loading :status="LoadingShow" msg="正在加载..." />
        <transition name="c">
            <div v-if="!LoadingShow">
                <h3  v-text="article.Title"></h3>
                <div v-html="article.Content"></div>
            </div>
        </transition>
    </div>
</template>

<script>
    import Loading from "./Loading"
    export default {
        name: "About2",
        components:{Loading},
        data(){return { article:{Title:"",Content:""},LoadingShow:true }},
        created(){
            let ajax=new XMLHttpRequest();
            //获取详细内容接口：/api/getContent?Id=?
            ajax.open("get",`/api/getContent?Id=${this.$route.params.Id}`,true);
            ajax.send();
            ajax.onload=()=>{
                let d=JSON.parse(ajax.responseText).currentContent
                this.article=d;
                this.LoadingShow=false;
            }
        }
    }
</script>
<style scoped>
    .Content{
        padding:0.15rem;
    }
    .Content h3{
        margin-bottom: 20px;
    }
    .c-enter{ opacity: 0;} /*显示的开始*/
    .c-enter-active{ transition: all 0.3s}/* 包含显示的开始与显示的结束 */
    .c-enter-to{ opacity: 1;}/*显示的结束*/
    /*隐藏*/
    .c-leave{ opacity: 1}/*隐藏的开始*/
    .c-leave-active{ transition: all 1s}/*包含隐藏的开始与隐藏的结束 */
    .c-leave-to{ opacity: 0}/*隐藏的结束*/
</style>
