<template>
    <div>
        <Loading :status="LoadingShow" msg="正在加载..." />
        <transition name="c">
            <div v-if="!LoadingShow"  v-html="html" class="Contact">
                <!--联系-->
            </div>
        </transition>
    </div>
</template>

<script>
    import Loading from "../Loading"
    export default {
        name: "Contact",
        components:{Loading},
        data(){return{ html:"",LoadingShow:true}},
        created(){
            let ajax=new XMLHttpRequest();
            //http://192.168.8.8:82/api/getConentList?Url=/contact&page=1
            ajax.open("get",`/api/getConentList?Url=${this.$route.path}&page=1`);
            ajax.send();
            ajax.onload=()=>{
                let d=JSON.parse(ajax.responseText);
                this.html=d.pNav[0].PageContent
                this.LoadingShow=false;
            }
        }
    }
</script>
<style scoped>
    .Contact{ padding:0.2rem; overflow-x: hidden; overflow-y: auto}
    .c-enter{ opacity: 0;} /*显示的开始*/
    .c-enter-active{ transition: all 0.3s}/* 包含显示的开始与显示的结束 */
    .c-enter-to{ opacity: 1;}/*显示的结束*/
    /*隐藏*/
    .c-leave{ opacity: 1}/*隐藏的开始*/
    .c-leave-active{ transition: all 1s}/*包含隐藏的开始与隐藏的结束 */
    .c-leave-to{ opacity: 0}/*隐藏的结束*/
</style>
