<template>
    <div class="Home">
        <!--banner区域-->
        <div class="banner loading">
            <Banner :data="banner" v-if="banner" />
        </div>
        <!--product区域-->
        <div class="product container">
            <div class="title loading" v-text="product.head.title"></div>
            <div class="subtitle loading" v-text="product.head.subtitle"></div>
            <ul>
                <li v-for="item in product.list" :key="item.Id" data-animation="fadeIn">
                    <router-link :to="item.Url">
                        <div class="Img_box loading">
                            <img v-if="item.Img" :src="item.Img"/>
                        </div>
                        <div class="Txt_box">
                            <div class="Title loading" v-text="item.Title"></div>
                           <div class="price loading" > 价格：￥{{item.price}}</div>
                        </div>
                    </router-link>
                </li>
            </ul>
            <router-link :to="product.more.href" class="more  loading" v-text="product.more.text">
            </router-link>
        </div>
        <!--about关于我们-->
        <div class="about" ref="about">
            <div class="container">
                <div class="title loading" v-text="about.head.title" ></div>
                <div class="subtitle loading" v-text="about.head.subtitle"></div>
            </div>
            <div class="description">
                <p class="loading" v-text="about.description!==''?about.description.substr(0,35)+'...':''"></p>
            </div>
            <div class="Img_box loading">
                <img :src="about.Img" v-if="about.Img">
            </div>
            <div class="container">
                <router-link :to="about.more.href" class="more loading" v-text="about.more.text"></router-link>
            </div>
        </div>
        <!--明显代言区域-->
        <div class="star">
            <div class="container">
                <div class="title loading" v-text="star.head.title"></div>
                <div class="subtitle loading" v-text="star.head.subtitle"></div>
            </div>
            <ul>
                <li v-for="item in star.list" :key="item.Id">
                    <router-link :to="item.Url">
                        <div class="Img_box loading">
                            <img :src="item.Img" v-if="item.Img">
                        </div>
                        <div class="Text_box">
                            <div class="titles loading" v-text="item.Title"></div>
                            <div class="types loading" v-text="item.Author"></div>
                            <div class="descriptions loading" v-text="item.Description!==''?item.Description.substr(0,30)+'...':''"></div>
                        </div>
                    </router-link>
                </li>
            </ul>
        </div>
        <!--音乐资讯区域-->
        <div class="gigbeat">
            <div class="container">
                <div class="title loading" v-text="gigbeat.head.title"></div>
                <div class="subtitle loading" v-text="gigbeat.head.subtitle"></div>
            </div>
            <ul>
                <li v-for="item in gigbeat.list" :key="item.Id">
                    <router-link :to="item.Url">
                        <div class="Date_box">
                            <div class="md loading" v-text="item.Date?(new Date(item.Date).getMonth()+1)+'-'+new Date(item.Date).getDate():''"></div>
                            <div class="year loading" v-text="item.Date?new Date(item.Date).getFullYear():''"></div>
                        </div>
                        <div class="titles loading" v-text="item.Title"></div>
                        <p class="descriptions loading" v-text="item.Description!==''?item.Description.substr(0,40)+'...':''"></p>
                    </router-link>
                </li>
            </ul>
            <div class="container">
                <router-link :to="gigbeat.more.href" class="more loading" v-text="gigbeat.more.text"></router-link>
            </div>
        </div>
    </div>
</template>
<script>
    import Banner from "./Banner";
    export default {
        name: "Home",
        components:{Banner},
        data(){
            return {
                banner:null,
                product:{/*产品中心的数据*/
                    head:{title:"",subtitle:""},
                    list:[
                        {Id:1,Title:"",Url:"",Img:"",price:""},
                        {Id:2,Title:"",Url:"",Img:"",price:""},
                        {Id:3,Title:"",Url:"",Img:"",price:""},
                        {Id:4,Title:"",Url:"",Img:"",price:""},
                        {Id:5,Title:"",Url:"",Img:"",price:""},
                        {Id:6,Title:"",Url:"",Img:"",price:""},
                        {Id:7,Title:"",Url:"",Img:"",price:""},
                        {Id:8,Title:"",Url:"",Img:"",price:""}
                    ],
                    more:{text:"",href:""}
                },
                about:{/*关于我们的数据*/
                    head:{title:"",subtitle:""},
                    description:"",
                    Img:"",
                    more:{text:"",href:""}
                },
                star:{/*明星代言的数据*/
                    head:{title:"",subtitle:""},
                    list:[
                        {Id:1,Title:"",Url:"",Img:"",Author:"",Description:""},
                        {Id:2,Title:"",Url:"",Img:"",Author:"",Description:""},
                        {Id:3,Title:"",Url:"",Img:"",Author:"",Description:""},
                        {Id:4,Title:"",Url:"",Img:"",Author:"",Description:""},
                    ]
                },
                gigbeat:{/*音乐资讯的数据*/
                    head:{title:"",subtitle:""},
                    list:[
                        {Id:1,Title:"",Url:"",Img:"",Date:"",Description:""},
                        {Id:2,Title:"",Url:"",Img:"",Date:"",Description:""},
                        {Id:3,Title:"",Url:"",Img:"",Date:"",Description:""},
                        {Id:4,Title:"",Url:"",Img:"",Date:"",Description:""},
                    ],
                    more:{text:"",href:""}
                }
            }
        },
        created(){
            //0.banner请求ajax
            let _ajax=new XMLHttpRequest();
            //http://192.168.8.8:82/api/getColumn?Nav=产品
            _ajax.open("get","/api/getBanner",true);
            _ajax.send();
            //使用 ajax.onload 代替 ajax.onreadystatechange
            _ajax.onload=()=>{
                this.banner=JSON.parse(_ajax.responseText);
            }

            //1.产品的ajax
            let ajax=new XMLHttpRequest();
            //http://192.168.8.8:82/api/getColumn?Nav=产品
            ajax.open("get","/api/getColumn?Nav=产品",true);
            ajax.send();
            //使用 ajax.onload 代替 ajax.onreadystatechange
            ajax.onload=()=>{
                this.product=JSON.parse(ajax.responseText).Column
            }

            //2.关于的ajax
            let ajax2=new XMLHttpRequest();
            //http://192.168.8.8:82/api/getColumn?Nav=关于
            ajax2.open("get","/api/getColumn?Nav=关于",true);
            ajax2.send();
            //使用 ajax.onload 代替 ajax.onreadystatechange
            ajax2.onload=()=>{
                // console.log(JSON.parse(ajax2.responseText))
                this.about=JSON.parse(ajax2.responseText).Column
            }
            //3.明星ajax
            let ajax3=new XMLHttpRequest();
            //http://192.168.8.8:82/api/getColumn?Nav=明星代言
            ajax3.open("get","/api/getColumn?Nav=红木种类",true);
            ajax3.send();
            //使用 ajax.onload 代替 ajax.onreadystatechange
            ajax3.onload=()=>{
                this.star=JSON.parse(ajax3.responseText).Column
            }
            //3.音乐资讯ajax
            let ajax4=new XMLHttpRequest();
            //http://192.168.8.8:82/api/getColumn?Nav=音乐资讯
            ajax4.open("get","/api/getColumn?Nav=红木资讯",true);
            ajax4.send();
            //使用 ajax.onload 代替 ajax.onreadystatechange
            ajax4.onload=()=>{
                this.gigbeat=JSON.parse(ajax4.responseText).Column
            }

        }
    }
</script>
<style scoped>
    
    .banner{
        height: 1.6rem;
        width: 100%;
    }
    .product{
    }
    .title{
        margin-top:0.3rem;
        line-height:0.32rem;
        font-size: 0.18rem;
        height: 0.32rem;
        text-align: center;
    }
    .subtitle{
        margin-top: 0.01rem;
        line-height:0.25rem;
        font-size: 0.14rem;
        height: 0.25rem;
        margin-bottom: 0.2rem;
        text-align: center;
    }
    .more{
        display: block;
        height: 0.4rem;
        line-height: 0.4rem;
        text-align: center;
        font-size: 0.17rem;
        margin-top: 0.3rem;
        border:1px solid;
        background: #b48978;
        color: white!important;
        
    }
    .product ul{
        overflow: hidden;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .product ul li{
        width: 1.68rem; height: 2rem;
        box-sizing: border-box;
        margin-bottom: 0.13rem;
    }
    .product ul li .Img_box{
        height:1.4rem; width: 100%;
    }
    .product ul li .Img_box img{
        width: 100%; height: 100%;
    }
	.product .Txt_box{ margin-top:0.05rem;}
    .product ul li .Title{height:0.23rem;margin-top: 1px;font-size: 0.16rem;text-align: center;}
    .product ul li .price{height:0.23rem;margin-top: 1px;font-size: 0.13rem;text-align: center;color: red;}
    .about .description{
        padding:0.2rem;
        overflow: hidden;
    }
    .about .description p{
        height: 100%;
    }
    .about .Img_box{
        width: 100%;
        height: 2.48rem;
    }
    .about .Img_box img{
        width: 100%; height: 100%;
        display: block;
    }
    .star ul li{
        padding:0.26rem 0;
        border-bottom: 1px solid #ddd;
    }
    .star ul{ padding:0 0.2rem}
    .star ul li a{
        display: flex;
    }
    .star ul li a .Img_box{
        width:0.87rem;height:0.87rem;
    }
    .star ul li a .Img_box img{
        width:inherit; height:inherit;
		border-radius: 50%;
    }
    .star ul li a .Text_box{
        height: 100%;
        margin-left: 0.13rem;
        flex-grow: 1;
    }
    .star ul li a .Text_box .titles{
        height: 0.28rem;
        
    }
    .star ul li a .Text_box .types{
        height: 0.28rem;
        margin:1px 0;
        color: #222;
         font-size: 0.15rem;
    }
    .star ul li a .Text_box .descriptions{
        font-size: 0.14rem;
        color: #555;
    }
    .gigbeat ul{
        padding:0 0.2rem
    }
    .gigbeat ul li{
         padding:0.26rem 0;
         border-bottom: 1px solid #ddd;
    }
    .gigbeat ul li .md{
        height: 0.25rem;
        line-height: 0.25rem;
        font-size: 0.25rem;
        margin-bottom: 1px;
        width: 50%;
    }
    .gigbeat ul li .year{
        width: 50%;
        height: 0.18rem;
        line-height: 0.18rem;
        font-size: 12px;
        margin-bottom: 1px;
    }
    .gigbeat ul li .titles{
	    margin-top:0.13rem;
        height: 0.3rem;
        margin-bottom: 1px;
    }
    .gigbeat ul li .descriptions{
        font-size: 0.14rem;
        color: #555;
    }
</style>