<template>
  <div id="app">

    <!--导航区域-->
    <div id="nav">
        <Nav/>
    </div>
    <!--主要内容区域-->
    <div id="content" :class="{openNav:openNavData}">
       <!--头部-->
       <div class="head">
           <!-- @touchstart 移动端的点击事件-->
            <div class="navBtn" @touchstart="openNavData=true">
                <span class="line1"></span>
                <span class="line2"></span>
                <span class="line3"></span>
            </div>
           <!--logo图片-->
           <!-- <router-link to="/">
                <img :src="logo" class="logo">
           </router-link> -->
       </div>
       <!--内容-->
       <div  class="main">
            <router-view/>
       </div>

       <!--遮罩-->
       <div class="zhezhao" @touchstart="openNavData=false"></div>
    </div>
  </div>
</template>
<script>
    import logoImg from "./assets/logo.png";
    import Nav from "./components/Nav"
    export default {
        data(){
            return {
                openNavData:false,
                logo:logoImg
            }
        },
		methods:{
			animations(){
				//1.选中有 data-ani属性的标签 这种标签 表示要播放动画
				var div=document.querySelectorAll("[data-animation]");
				//2.页面刷新时 把已经在可视区的 标签加载动画显示
				for(var n=0;n<div.length;n++){
					if(div[n].getBoundingClientRect().top+60<document.documentElement.clientHeight){
						div[n].classList.add("animated");
						div[n].classList.add(div[n].dataset.animation);
					}
				}
				//3.选中有滚动条的标签 main 加滚动事件
				document.querySelector("#content .main").onscroll=function(){
					for(var n=0;n<div.length;n++){
						if(div[n].getBoundingClientRect().top+60<document.documentElement.clientHeight){
							div[n].classList.add("animated")//使用animated.css 必须加这个
							div[n].classList.add(div[n].dataset.animation);//使用data-animate上设置的动画名称
						}
					}
				}
			}
		},
		mounted(){
			this.animations();
		},
        components:{Nav},
        watch:{
            $route(){
                /*当组件的路由守卫不能使用的时候 可以使用watch来监控
                  $route中路由地址的变化
                */
                //console.log("地址有变化了")
                //监控地址有变化的时候 就把导航折叠掉
                this.openNavData=false;
            }
        }
    }
</script>

<style>
    @import url("./assets/animate.min.css");
    /*
    @公共的样式
    */
    html{
        font-size:calc(100vw / 3.75);
    }
    body{
        margin:0;
        font-size: 16px;
        overflow: hidden;
    }
    ul{
        list-style: none;
    }
    ul,h1,h2,h3,p{
        padding:0;
        margin:0;
    }
    a{
        text-decoration: none;
        -webkit-tap-highlight-color: transparent;
    }
    .loading:empty{
        position: relative;
        background: #eee;
        overflow: hidden;
    }
    .container{
        padding-left: 0.13rem;
        padding-right: 0.13rem;
    }
    /*.loading:empty{ !*表示选择空标签的loading*!
        border:3px solid red;
    }*/
    /*如果loading表示里面 是空的 没有子标签的的情况下  我就给它加一个伪元素after 加载动画*/
    .loading:empty:after{
        content: "";
        position: absolute;
        width: 100%; height: 100%;
        background:linear-gradient(90deg,transparent,#dedede,transparent);
        transform: translate(-100%,0);
        opacity: 0;
        animation: loadingIn 1s infinite;
    }
    @keyframes loadingIn{
        to{
            opacity: 1;
            transform: translate(100%,0);
        }
    }







    /*导航区显示的样式*/
    #nav{
      width: 2.60rem;
      height:100vh;
      background:#b48978;
      position: absolute;
      top:0;left:0;
      overflow: hidden;
    }
  /*主要内容区域样式*/
  #content{
     width: 100%;
     height: 100vh;
     position: relative;
     z-index: 1;
     transition: all 0.3s;
     /* box-shadow: -5px 0 30px white; */
  }
  #content .head{
     height: .50rem;
     width: 100%;
     background: rgba(161, 16, 0, 0.5);
     position: relative;
	 overflow: hidden;
  }
  #content .main{
      width: 100%;
      height: calc(100% - .50rem);
      background: #f9f6f7;
      overflow-x: hidden;
      overflow-y: auto;
  }
  /*导航按钮*/
    #content .navBtn{
        position: absolute;
        height: .50rem;
        width: .58rem;
        padding:0.16rem 0.13rem;
        box-sizing: border-box;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;
        overflow: hidden;
    }
    #content .navBtn span{
        width: 100%;
