<template>
    <div class="Nav">
        <div class="top"></div>
        <ul class="navList">
            <!--循环顶级导航-->
            <li v-for="(pNav,k) in navData" :key="pNav.Id" @touchstart="openChildNav(pNav)">
                <!--event  是router-link上的一个属性 用来设置通过什么事件 来触发这个路由跳转
                  默认情况下是click，在移动端中可以改成 touchstart
                  :exact="true"  表示绝对匹配，  :exact="false" 表示不绝对匹配
                -->


                <!--如果没有子导航 就显示可以点击的链接-->
                <router-link v-if="pNav.children===undefined" :to="pNav.Url" event="touchstart" :exact="pNav.Url==='/'">
                    {{pNav.Nav}}
                </router-link>
                <!--如果有子导航 就显示不可点击的链接-->
               <a href="javascript:;" v-else  :class="{'router-link-active':pNav.show}">
                   {{pNav.Nav}}
               </a>


                <ul v-if="pNav.children" ref="ul" :class="{ulShow:!pNav.show}"  @touchstart.stop>
                    <!--循环二级导航-->
                    <li v-for="cNav in pNav.children" :key="cNav.Id">
                        <router-link :to="cNav.Url" event="touchstart">
                            {{cNav.Nav}}
                        </router-link>
                    </li>
                </ul>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "Nav",
        data(){
           return{
               navData:[
                   {Id:1,Nav:"首页",Url:"/"},
                   {Id:2,Nav:"产品",Url:"/product"},
                   {Id:3,Nav:"关于",Url:"/about",children:[
                           {Id:31,Nav:"关于1",Url:"/about/about1"},
                           {Id:32,Nav:"关于2",Url:"/about/about2"},
                           {Id:33,Nav:"关于3",Url:"/about/about3"},
                    ],show:false},
                   {Id:4,Nav:"音乐资讯",Url:"/gigbeat"},
                   {Id:5,Nav:"联系",Url:"/contact"},
               ]
           }
        },
        methods:{
            openChildNav(pNav){
                //1.把当前点击 导航的子导航 进行显示或隐藏
                pNav.show=!pNav.show;
                //2.把当前点击导航的 兄弟导航的子导航隐藏
                this.navData.forEach((v,k)=>{
                    if(v!==pNav){ //判断只要不是自己点击的 都改成隐藏
                        v.show=false;
                    }
                })

            }
        },
        mounted(){
            this.$refs.ul.forEach((v,k)=>{
                //v.scrollHeight 可得到 当前ul中 所有li占据的总高度
                v.style.height=v.scrollHeight+'px'
            });
            /*检查当前 地址栏中的path 与5个导航中Url属性对比，如果有匹配的
             就把匹配的导航的v.show改成true，这样默认页面刷新时这个导航的子导航就会显示*/
            this.navData.forEach((v,k)=>{
                v.show= this.$route.path.startsWith(v.Url)
            })

        },
        /*ajax请求 可以写在created也可以写在mounted生命周期函数中*/
        created(){
            let ajax=new XMLHttpRequest();
            ajax.open("get","/api/topNav",false);
            ajax.send();
            let ajaxData=JSON.parse(ajax.responseText);
            ajaxData.unshift({Id:-1,Nav:"首页",Url:"/"})
            this.navData=ajaxData;
        }
    }
</script>

<style scoped>
    .top{ height:0.5rem; background: #555}
    .navList li{
        border-top:1px solid #777;
        text-indent: 0.3rem;
    }
    .navList li a{
        display: block;
        height:0.5rem; line-height: 0.5rem;
        font-size: 0.13rem;
        color:white
    }
    /*子导航ul*/
    .router-link-active{
        background:#444;
    }
    .navList li ul{
        transition: all 0.3s;
        overflow: hidden;
    }
    .ulShow{
        height: 0px !important;
    }
</style>