<template>
    <transition name="x" >
        <div class="loadingBox" v-if="status">
                <div class="Loading" >
                    <img :src="Img"/>
                    <div v-text="msg"></div>
                </div>
        </div>
    </transition>
</template>

<script>
    import loadImg from "../assets/placeholder.svg"
    import Loading from "./Loading"
    export default {
        name: "Loading",
        props:["msg","status"],
        data(){ return {Img:loadImg}}
    }
</script>

<style scoped>
    .loadingBox{
        position: absolute;
        width: 100%; height: 100vh;
        left:0;top:0;
        z-index: 9999;
    }
    .Loading{
        padding:0.1rem 0.2rem;
        position: absolute;
        text-align: center;
        top:35%;left:50%;
        transform: translate(-50%,0%);
        color:#555;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
    }
    .Loading img{
        width: 25px;
        margin-right: 5px;
    }
    .x-enter{ opacity: 0;} /*显示的开始*/
    .x-enter-active{ transition: all 0.2s}/* 包含显示的开始与显示的结束 */
    .x-enter-to{ opacity: 1;}/*显示的结束*/
    /*隐藏*/
    .x-leave{ opacity: 1}/*隐藏的开始*/
    .x-leave-active{ transition: all 0.4s}/*包含隐藏的开始与隐藏的结束 */
    .x-leave-to{ opacity: 0}/*隐藏的结束*/
</style>