<template>
    <div >

        <!--音乐资讯区域-->
        <div class="gigbeat">
            <ul>
                <li v-for="item in gigbeat.list" :key="item.Id">
                    <router-link :to="item.Url">
                        <div class="Date_box">
                            <div class="md loading"
                                 v-text="item.Date?(new Date(item.Date).getMonth()+1)+'-'+new Date(item.Date).getDate():''">
                            </div>
                            <div class="year loading" v-text="item.Date?new Date(item.Date).getFullYear():''"></div>
                        </div>
                        <div class="titles loading" v-text="item.Title"></div>
                        <p class="descriptions loading" v-text="item.Description!==''?item.Description.substr(0,40)+'...':''"></p>
                    </router-link>
                </li>
            </ul>
            <!-- 分页区域-->
            <div v-if="gigbeat.page" class="page container">
                <router-link  :class="{pageActive:gigbeat.page.page===item?'red':''}"  :to="`${$route.path}?page=${item}`" v-for="item in gigbeat.page.pageList" :key="item.id">
                    {{item}}
                </router-link>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: "Home",
        data(){
            return {
                gigbeat:{/*音乐资讯的数据*/
                    list:[
                        {Id:1,Title:"",Url:"",Img:"",Date:"",Description:""},
                        {Id:2,Title:"",Url:"",Img:"",Date:"",Description:""},
                        {Id:3,Title:"",Url:"",Img:"",Date:"",Description:""},
                        {Id:4,Title:"",Url:"",Img:"",Date:"",Description:""},
                    ],
                    page:null
                }
            }
        },
        methods:{
            ajax(num){
                let ajax=new XMLHttpRequest();
                ////获取 音乐资讯 的数据 http://192.168.8.8:82/api/getConentList?Url=/gigbeat&page=1
                ajax.open("get",`/api/getConentList?Url=${this.$route.path}&page=${num}`);
                ajax.send();
                ajax.onload=()=>{
                    this.gigbeat.list=JSON.parse(ajax.responseText).content;
                    this.gigbeat.page=JSON.parse(ajax.responseText).pageInfo;
                }
            }
        },
        created(){
			//如果地址上没有？page=xx这种地址 就传入1 显示第一页的数据
            // console.log("获取问号后面的数据：",this.$route.query.page)
           this.ajax(this.$route.query.page||1)
        },
        watch:{
            $route(){
				//点击分页的时候 获取地址上的?page 然后重新调用ajax
                // console.log("地址有变")
                this.ajax(this.$route.query.page)
            }
        }
    }
</script>
<style scoped>
    .gigbeat{
        padding-bottom: 1rem
    }
    .title{
        margin:0.3rem 0;
        line-height:0.32rem;
        font-size: 0.18rem;
        height: 0.32rem;
        text-align: center;
    }
    .subtitle{
        margin-top: 0.01rem;
        line-height:0.25rem;
        font-size: 0.14rem;
        height: 0.25rem;
        margin-bottom: 0.2rem;
        text-align: center;
    }
    .more{
        display: block;
        height: 0.4rem;
        line-height: 0.4rem;
        text-align: center;
        font-size: 0.17rem;
        margin-top: 0.3rem;
    }
    .gigbeat ul{
        padding:0 0.2rem
    }
    .gigbeat ul li{
        padding:0.26rem 0;
        border-bottom: 1px solid #ddd;
    }
    .gigbeat ul li .md{
        height: 0.25rem;
        line-height: 0.25rem;
        font-size: 0.25rem;
        margin-bottom: 1px;
        width: 50%;
    }
    .gigbeat ul li .year{
        width: 50%;
        height: 0.18rem;
        line-height: 0.18rem;
        font-size: 12px;
        margin-bottom: 1px;
    }
    .gigbeat ul li .titles{
        margin-top:0.13rem;
        height: 0.3rem;
        margin-bottom: 1px;
    }
    .gigbeat ul li .descriptions{

    }
    .page{
        margin-top: 0.2rem;
        text-align: center;
    }
    .page a{
        display: inline-block;
        line-height: 0.25rem;
        border:1px solid;
        padding:0 0.15rem
    }
    .pageActive{
        background: black;
        color:white;
    }
</style>