<template>
    <div class="container">
        <ul>
            <li v-for="item in product.list" :key="item.Id" data-animation="fadeIn">
                <router-link :to="item.Url">
                    <div class="Img_box loading">
                        <img v-if="item.Img" :src="item.Img"/>
                    </div>
                    <div class="Txt_box">
                        <div class="Title loading" v-text="item.Title">
                        </div>
                        <div class="price loading" >价格：￥{{item.price}}</div>
                    </div>
                </router-link>

            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "ProductList",
        data(){
            return {
                head_height:0,
                product:{
                    list:[
                        {Id:1,Title:"",Url:"",Img:"",price:""},
                        {Id:2,Title:"",Url:"",Img:"",price:""},
                        {Id:3,Title:"",Url:"",Img:"",price:""},
                        {Id:4,Title:"",Url:"",Img:"",price:""},
                        {Id:5,Title:"",Url:"",Img:"",price:""},
                        {Id:6,Title:"",Url:"",Img:"",price:""},
                        {Id:7,Title:"",Url:"",Img:"",price:""},
                        {Id:8,Title:"",Url:"",Img:"",price:""}
                    ]
                }
            }
        },
        methods:{
          ajax1(){
              //console.log(`/api/getConentList?Url=${this.$route.path}&page=1`)
              let ajax=new XMLHttpRequest();
              //根据导航地址显示 导航下的列表数据
              //http://192.168.8.8:82/api/getConentList?Url=/product/product1&page=1
              ajax.open("get",`/api/getConentList?Url=${this.$route.path}&page=1`);
              ajax.send();
              ajax.onload=()=>{
                  let d=JSON.parse(ajax.responseText)
                  this.product.list=d.content;
                  if(d.pNav.length>1){
                      this.$parent.head_text="全部"
                  }else{
                      this.$parent.head_text=d.pNav[0].Nav
                  }
              }
          }
        },
        created(){
            this.ajax1();
        },
        watch:{
            $route(){
                this.ajax1();
            }
        }
    }
</script>

<style scoped>
    .product ul{
        overflow: hidden;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .product ul li{
        width: 1.68rem; height: 1.8rem;
        box-sizing: border-box;
        margin-bottom: 0.13rem;
        text-align: center;
    }
    .product ul li .Img_box{
        height:1.3rem; width: 100%;
    }
    .product ul li .Img_box img{
        width: 100%; height: 100%;
    }
    .product ul li .Title{height:0.23rem;margin-top: 1px;font-size: 0.16rem}
    .product ul li .price{height:0.23rem;margin-top: 1px;font-size: 0.13rem;color: red;}
</style>
