<template>
    <div class="Home">
        <!--product区域-->
        <div class="product">
            <div class="title" @touchstart="toggle">
				<div class="child_nav container">
                    <div class=" loading">
                        <span  v-if="head_text">{{head_text}}</span>
                        <span class="icon" v-if="head_text"></span>
                    </div>
				</div>
				<div class="child_nav_list" :style="{height:head_height}" ref="child_nav" >
					<router-link :to="item.Url"
                                  event="touchstart"
                                  v-for="item in product.head.nav"
                                  :key="item.Id" class="container">
                        {{item.Nav}}
                    </router-link>
				</div>
			</div>
            <router-view/>
        </div>
    </div>
</template>
<script>
    export default {
        name: "Home",
        data(){
            return {
				head_height:0,
                head_toggle:false,
                head_text:null,
                product:{/*产品中心的数据*/
                    head:{
						nav:[
							{Id:1,Nav:"11",Url:""},
							{Id:2,Nav:"22",Url:""},
							{Id:3,Nav:"33",Url:""},
							{Id:4,Nav:"44",Url:""}
						]
					}
                }
            }
        },
		methods:{
			toggle(){
			    if(this.head_toggle){
                    this.head_height=0;
                    this.head_toggle=false;
                }else{
                    if(this.$refs.child_nav){
                        this.head_height=this.$refs.child_nav.scrollHeight+'px'
                    }
                    this.head_toggle=true;
                }
			}
		},
        created(){
            let ajax=new XMLHttpRequest();
            //http://192.168.8.8:82/api/getChildNav?Url=/product
            ajax.open("get","/api/getChildNav?Url=/product",true);
            ajax.send();
            ajax.onload=()=>{
                this.product.head.nav=JSON.parse(ajax.responseText);
            }
        }
    }
</script>
<style scoped>
    .product{
    }
    .title{
        line-height:0.40rem;
        min-height: 0.40rem;
		position: relative;
		z-index: 999;
    }
    .title .child_nav{
        height:0.40rem ;
        box-sizing: border-box;
        padding-top:0.05rem;
        padding-bottom:0.05rem;
    }
	 .title .child_nav>div{
		display:flex;
		justify-content:space-between;
		align-items: center;
        height:100%;
	 }
	 .title .child_nav .icon{
		width:0.09rem;height:0.09rem;
		border-bottom:1px solid transparent;
		border-right:1px solid transparent;
		border-left:1px solid;
		border-top:1px solid;
		transform:translate(0,-0.025rem) rotate(-135deg)
	 }
	 .title .child_nav_list{
		overflow:hidden;
		transition:all 0.5s;
		width:100%;
		position: absolute;
		background:white;
	 }
	 .title .child_nav_list a{
		display:block;
		border-bottom:1px solid #7a584e;
	 }
    .subtitle{
        margin-top: 0.01rem;
        line-height:0.25rem;
        font-size: 0.14rem;
        height: 0.25rem;
        margin-bottom: 0.2rem;
        text-align: center;
    }
    .more{
        display: block;
        height: 0.4rem;
        line-height: 0.4rem;
        text-align: center;
        font-size: 0.17rem;
        margin-top: 0.3rem;
    }
    .router-link-active{
        background: #b48978;
    }
</style>