<template>
        <swiper  :options="swiperOption" style="height:100%">
            <!-- slides  swiper-slide 是就是swiperSlide-->
            <swiper-slide v-for="item in data" :key="item.Id" >
                <router-link :to="item.Url">
                    <img :src="item.Img" width="100%">
                </router-link>
            </swiper-slide>

            <!-- 小圆点 -->
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
</template>

<script>
    import { swiper, swiperSlide } from 'vue-awesome-swiper'
    export default {
        props:["data"],
        data(){
            return {
                swiperOption: {
                    loop: true, // 支持无缝轮播
                    autoplay: { //自动播放
                        disableOnInteraction: false,//表示永远不停止 autoPlay
                        delay:5000,//表示5秒自动滚动一下
                    },
                    pagination: {
                        el: '.swiper-pagination',
                        clickable :true,//可以实现点击小圆点切换效果
                        bulletClass : 'bullet',//所有小圆点按钮class名称
                        bulletActiveClass : 'bulletActive'//当前激活的小圆点按钮class名称
                    }
                }
            }
        },
        components: { //这个2个是官方拿的 固定的
            swiper,
            swiperSlide
        }
    }
</script>

<style>
    .bullet{
        display: inline-block;
        width: 5px; height:5px;
        border-radius: 50%;
        background: rgba(255,255,255,0.5);
        margin:0 8px;
    }
    .bulletActive{
        background: white;
    }

</style>